<template>
    <div class="form-container">
      <h2>Enviar Recomendación</h2>
      <form @submit.prevent="handleSubmit" class="recommendation-form">
        <div class="form-group">
          <input v-model="nombre" type="text" placeholder="Tu nombre" required class="form-input" />
        </div>
        
        <div class="form-group">
          <select v-model="tipo" required class="form-input">
            <option value="" disabled selected>Selecciona un tipo</option>
            <option>Serie</option>
            <option>Película</option>
            <option>Música</option>
            <option>Libro</option>
            <option>Otro</option>
          </select>
        </div>
        
        <div class="form-group">
          <textarea v-model="recomendacion" placeholder="Tu recomendación" required class="form-input"></textarea>
        </div>
        
        <div class="form-group">
          <input v-model="correo" type="email" placeholder="Tu correo (opcional)" class="form-input" />
        </div>
        
        <button type="submit" class="submit-btn">Enviar</button>
      </form>
  
      <p v-if="successMessage" class="success-message">{{ successMessage }}</p>
      <p v-if="errorMessage" class="error-message">{{ errorMessage }}</p>
    </div>
  </template>
  
  <script>
  import axios from "axios";
  
  export default {
    data() {
      return {
        nombre: "",
        tipo: "",
        recomendacion: "",
        correo: "",
        successMessage: "",
        errorMessage: "",
      };
    },
    methods: {
      async handleSubmit() {
        const data = {
          Nombre: this.nombre,
          Tipo: this.tipo,
          Recomendacion: this.recomendacion,
          Correo: this.correo,
        };
  
        try {
          const response = await axios.post(
            "https://api.sheetbest.com/sheets/4ee5fb70-b768-4108-b689-72638aa83409",
            data
          );
  
          if (response.data) {
            this.successMessage = "Recomendación enviada con éxito";
            this.errorMessage = "";
            
            // Limpiar formulario
            this.nombre = "";
            this.tipo = "";
            this.recomendacion = "";
            this.correo = "";
          } else {
            throw new Error(response.data.message);
          }
        } catch (error) {
          this.errorMessage = `Error al enviar la recomendación: ${error.message}`;
          this.successMessage = "";
        }
      },
    },
  };
  </script>
  
  <style scoped>
  .form-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  h2 {
    text-align: center;
    font-size: 24px;
    margin-bottom: 20px;
    color: #333;
  }
  
  .recommendation-form {
    display: flex;
    flex-direction: column;
  }
  
  .form-group {
    margin-bottom: 15px;
  }
  
  .form-input {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
  }
  
  textarea.form-input {
    resize: vertical;
    min-height: 100px;
  }
  
  .submit-btn {
    background-color: #4CAF50;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
  }
  
  .submit-btn:hover {
    background-color: #45a049;
  }
  
  .success-message {
    margin-top: 20px;
    color: green;
    text-align: center;
  }
  
  .error-message {
    margin-top: 20px;
    color: red;
    text-align: center;
  }
  </style>
  